<template lang="html">
    <div>
        <el-table
    ref="multipleTable"
    :data="tableData3"
    border
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="50">
    </el-table-column>
    <el-table-column
      prop="message"
      label="车辆信息"
      width="320"
     >

    </el-table-column>
    <el-table-column

      label="司机信息"
      width="230">
      <template scope="scope">
          {{scope.row.name}}
          {{scope.row.date}}
      </template>
    </el-table-column>
    <el-table-column
      prop="address"
      label="定位信息"
      width="320"
      show-overflow-tooltip>

    </el-table-column>
    <el-table-column

      label="操作"
      width="330">
      <template scope="scope">
        <p>{{scope.row.gongsi}}</p>
        <el-button

          size="small">
          {{ scope.row.shares }}
        </el-button>
        <el-button  size="small">
          {{ scope.row.jieshou }}
        </el-button>
        <el-button

          size="small">
          {{ scope.row.jujue }}
        </el-button>
      </template>
    </el-table-column>
  </el-table>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        tableData3: [{
          date: '2016-05-03',
          message: '沪DG5060 厢式车 9.6米 10吨 30放',
          name:'衣龙魁',
          address: '山东省烟台市莱阳市G204（龙门西路）',
          gongsi:'分享给：xxxx公司',
          shares:'取消分享'
        },
          {
            date: '2016-05-03',
            message: '沪DG5060 厢式车 9.6米 10吨 30放',
            name:'衣龙魁',
            address: '山东省烟台市莱阳市G204（龙门西路）',
            gongsi:'来自：xxxx公司的分享',
            shares:'取消分享'
          },{
            date: '2016-05-03',
            message: '沪DG5060 厢式车 9.6米 10吨 30放',
            name:'衣龙魁',
            address: '山东省烟台市莱阳市G204（龙门西路）',
            jieshou:'接受',
            jujue:'拒绝'
          }],
          multipleSelection: []
      }
    },

    methods: {
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
</script>
<style lang="less">
  el-table-column{
    height: 100px;
  }

</style>



